export function convertToDDS({ data, width, height }) {
  const DDS_MAGIC = 0x20534444;
  const DDS_HEADER_SIZE = 124;
  const DDS_PIXELFORMAT_SIZE = 32;

  const header = new ArrayBuffer(128);
  const dv = new DataView(header);

  // DDS magic number
  dv.setUint32(0, DDS_MAGIC, true);

  // Size of structure
  dv.setUint32(4, DDS_HEADER_SIZE, true);

  // Flags (Caps | Height | Width | PixelFormat | MipMapCount | Pitch)
  dv.setUint32(8, 0x0002100F, true);

  // Height & Width
  dv.setUint32(12, height, true);
  dv.setUint32(16, width, true);

  // Pitch or linear size (row size * height for uncompressed RGBA)
  dv.setUint32(20, width * 4, true);

  // MipMap count
  dv.setUint32(28, 1, true);

  // Pixel format
  dv.setUint32(76, DDS_PIXELFORMAT_SIZE, true); // size
  dv.setUint32(80, 0x00000041, true); // flags: RGB | ALPHAPIXELS
  dv.setUint32(84, 0, true);          // FourCC (none)
  dv.setUint32(88, 32, true);         // RGB bit count
  dv.setUint32(92, 0x00ff0000, true); // R
  dv.setUint32(96, 0x0000ff00, true); // G
  dv.setUint32(100, 0x000000ff, true); // B
  dv.setUint32(104, 0xff000000, true); // A

  // Caps
  dv.setUint32(108, 0x00001000, true);

  const ddsData = new Uint8Array(128 + data.length);
  ddsData.set(new Uint8Array(header), 0);
  ddsData.set(data, 128);

  return ddsData.buffer;
}
