import { decode as decodeJpeg } from 'jpeg-js';
import { loadImageFromBlob } from './image-loader';
import { convertToDDS } from './DDSWriter';

export async function ck3_builder(mod, files, gameFolder) {
  const modMusicFolder = gameFolder.folder("music").folder(mod.filename);
  const musicFolder = modMusicFolder;
  const musicCategoryFolder = gameFolder.folder("music").folder("music_player_categories");
  const inGameFolder = gameFolder.folder("music").folder("in_game");

  const text = [textHeader];
  const trackNames = [];
  let trackIndex = 1;

  if (mod.image) {
    try {
      const originalImage = await loadImageFromBlob(mod.image);

      const canvas = document.createElement('canvas');
      canvas.width = 648;
      canvas.height = 376;
      const ctx = canvas.getContext('2d');
      const tempCanvas = document.createElement('canvas');
      tempCanvas.width = originalImage.width;
      tempCanvas.height = originalImage.height;
      const tempCtx = tempCanvas.getContext('2d');
      tempCtx.putImageData(new ImageData(originalImage.data, originalImage.width, originalImage.height), 0, 0);
      ctx.drawImage(tempCanvas, 0, 0, 648, 376);

      const jpegBlob = await new Promise(resolve => canvas.toBlob(resolve, 'image/jpeg'));
      const jpegArrayBuffer = await jpegBlob.arrayBuffer();

      const rawImageData = decodeJpeg(new Uint8Array(jpegArrayBuffer), { useTArray: true });
      const ddsBuffer = convertToDDS(rawImageData);

      gameFolder
        .folder('gfx')
        .folder('interface')
        .folder('illustrations')
        .folder('music_player')
        .file(`${formatDisplayName(mod.filename)} Soundtrack.dds`, new Uint8Array(ddsBuffer));
    } catch (err) {
      console.error('Image processing failed:', err);
    }
  }

  files.forEach((f) => {
    const lowerCaseName = f.name.toLowerCase();
    const baseName = lowerCaseName.replace(/\.ogg$/, "");

    if (baseName !== "maintheme") {
      const trackId = `mx_${mod.filename.toLowerCase()}_${trackIndex++}`;
      text.push(textDef(trackId, baseName, mod));
      musicFolder.file(`${trackId}.ogg`, f);
      trackNames.push(`${trackId}`);
    } else {
      const mainThemeFolder = modMusicFolder.folder("main_themes");
      mainThemeFolder.file(`${mod.filename}_maintheme.txt`, textDef(baseName, baseName, mod));
      mainThemeFolder.file(`${baseName}.ogg`, f);
    }
  });

  inGameFolder.file(`${mod.filename}.txt`, text.join("\n"));

  const categoryText = `category = {
  id = "mx_category_${mod.filename.toLowerCase()}"
  name = "${formatDisplayName(mod.filename)} Soundtrack"
  tracks = {
    ${trackNames.join("\n    ")}
  }
}`;
  musicCategoryFolder.file(`${mod.filename}.txt`, categoryText);
}

function textDef(trackId, originalName, mod) {
  return `${trackId} = {
    music = "file:/music/${mod.filename}/${trackId}.ogg"
    name = "${formatDisplayName(originalName)}"
    pause_factor = 25
    mood = yes
    can_be_interrupted = yes
    is_prioritized_mood = no
    is_valid = { culture = cornish }
  }\n`;
}

function formatDisplayName(name) {
  return name
    .replace(/_/g, ' ')
    .replace(/\.ogg$/, '')
    .replace(/\.[^/.]+$/, '')
    .replace(/\b\w/g, c => c.toUpperCase());
}

const textHeader = `#testing build, Nothing is Fool proof this is being worked on`;
